<script>
	import UniPopup from '@/components/uni-popup/uni-popup.vue'
	import uniPopupMessage from '@/components/uni-popup/uni-popup-message.vue'
	// import uniPopupDialog from '@/components/uni-popup/uni-popup-dialog.vue'
	import uniPopupDrawer from '@/components/uni-popup/uni-popup-drawer.vue'
	import uniPopupMore from '@/components/uni-popup/uni-popup-more.vue'
	import uniPopupTwocode from '@/components/uni-popup/uni-popup-twocode.vue'

	import WucTab from '@/components/wuc-tab/wuc-tab.vue';
	import {
		obj2style
	} from '@/components/demo/utils/index';
	// const that=this;
	export default {
		name: "tab",

		components: {
			WucTab,
			UniPopup,
			uniPopupMessage,
			// uniPopupDialog,
			uniPopupDrawer,
			uniPopupMore,
			uniPopupTwocode

		},
		props: {
			globe: Object,
			theType: Object,
			tm: Object,
			smart: Object,
			sun: Object



		},
		provide() {
			return {
				popm: this,
				popup: this,
				arrobj: {
					theNumber: "123",
					thePrice: "123123"
				},
				closeDrawer: function() {
					const that = this;
					console.log(that, "我是父组件的方法")
					that.$refs.popup.close(

					)
				}
			}
		},
		data() {
			return {
				intro: {
					thePrice: NaN,
					theNumber: NaN,
					theOperators: "...",
					theDetail: "...",
					itemid: NaN

				},
				maskclick: false,
				tabList3: [{
					name: 'GLOBE/TM'
				}, {
					name: 'SMART/TNT'
				}, {
					name: 'SUN'
				}],
				TabCur3: 0,
				tab1: true,
				tab2: false,
				tab3: false,

				// ..........................
				status: 0,
				centerDialogVisible: false,
				downloadData: {
					url: 'https://segmentfault.com/hottest', //需要转化成二维码的网址
					// icon: require('./xxx.jpg')  //二维码中间的图片,可以不设置
				},
				newActiveName: "",
				activeName: "first",
				direction: "btt",
				drawer: false,


				apiUrl: 'https://ccc.wangwanglive.com/index.php/index/juanpay/',
				//apiUrl:'http://feic.com/index.php/index/juanpay/',

				size: "35%",

				opertaorsId: 1,
				tabValue: true,
				i: 0,

				tag: true,
				loading: false,
				// 二维码赋值
				val: "123123",



			};
		},

		methods: {
			// 禁止SWIPER滑动？



			// 支付成功后,全屏loading
			load(ad) {
				var _this = this;


				if (ad[0]) {
					this.$refs.popupmore.open()


					// _this.$refs.popupmore.close()//控制遮罩层关闭
					// #ifdef H5
					_this.$refs.popuptwocode.open()

					// #endif

					this.$refs.popup.close();

					// 弹出WX或者ALIPAY的支付二维码

				} else {
					_this.$refs.popupmore.close() //控制遮罩层关闭

				}





			},
			// 弹窗，1、验证（如果可以，禁止点击其他tabs），可点击，判断号码与运营商是否一致。2、装载相应数据。
			choicePrice(index, price, desc) {

				var _this = this;
				var num = this.theType.number;


				this.status = index
				this.intro.itemid = index
				// UNI-APP需要添加一个验证，当typeid不等于TabCur3时，return false

				if (this.opertaorsId != this.TabCur3) {
					this.$refs.popupm2.open()
					return false
				}

				//判断号码是否为号码段，符合10位
				var reg = /^[1][3,4,5,6,7,8,9][0-9]{9}$/;;


				// 验证步骤

				if (this.common.test(num) && num.match(reg)) {

					this.intro.thePrice = price
					// this.drawer = true
					this.intro.theDetail = desc
					this.$refs.popup.open(

					)
				} else {
					this.$refs.popupm.open()

				}


			}
		},
		computed: {
			CustomBar() {
				let style = {};
				// #ifdef MP-WEIXIN
				const systemInfo = uni.getSystemInfoSync();
				let CustomBar =
					systemInfo.platform === "android" ?
					systemInfo.statusBarHeight + 50 :
					systemInfo.statusBarHeight + 45;
				style['top'] = CustomBar + 'px';
				// #endif
				// #ifdef H5
				style['top'] = 0 + 'px';
				// #endif
				return obj2style(style);
			}
		},
		watch: {

			// 父组件通过PROPS（theType）传值到子组件，通过watch监控props值改变，修改相对应的值
			//**如果PROPS为OBJ，WATCH无法监控其属性,因为在父组件时，修改的是其属性。而对象未发生改变**
			TabCur3() {
				switch (this.TabCur3) {
					case 0:
						this.tab1 = true;
						this.tab2 = false;
						this.tab3 = false;
						break;
					case 1:
						this.tab1 = false;
						this.tab2 = true;
						this.tab3 = false;
						break;
					case 2:

						this.tab1 = false;
						this.tab2 = false;
						this.tab3 = true;
						break
				}
			},
			theType() {
				this.intro.theNumber = this.theType.number;
				this.tabValue = this.theType.value;

				this.i = 0;
				switch (this.theType.id) {
					case 1:
						this.TabCur3 = 0;

						this.opertaorsId = 0;
						this.intro.theOperators = "GLOBE/TM";
						this.tab1 = true;
						this.tab2 = false;
						this.tab3 = false;


						break;
					case 2:
						this.TabCur3 = 1;


						this.opertaorsId = 1;
						this.intro.theOperators = "SMART/TNT";
						this.tab1 = false;
						this.tab2 = true;
						this.tab3 = false;

						break;
					case 3:
						this.TabCur3 = 2;

						this.opertaorsId = 2;
						this.intro.theOperators = "SUN";
						this.tab1 = false;
						this.tab2 = false;
						this.tab3 = true;

						break;

				}

			}

		},

	}
</script>

